  const handleDeleteProduct = (row, index) => {
    console.log('deleting product...')
    const newProducts = [...sales.products];
    newProducts.splice(index, 1);
    setSales((prev) => ({ ...prev, products: newProducts }));
  };

  const handleProductChange = (index, field, fieldValue) => {

    if (field === 'discount') {
      if (fieldValue < 0) {
        fieldValue = 0;
        enqueueSnackbar(`Discount should be between 0 and 100 percent.`, { variant: 'error' })
      } else if (fieldValue > 100) {
        fieldValue = 100;
        enqueueSnackbar(`Discount should be between 0 and 100 percent.`, { variant: 'error' })
      }
    };

    // if (field === 'sellingPrice') {
    //   if (fieldValue < sales.products[index].product.sellingPrice) {
    //     fieldValue = sales.products[index].product.sellingPrice;
    //     console.log("fieldValue", fieldValue)
    //     enqueueSnackbar(`The product price cannot be lower than its selling price of ${format(sales.products[index].product.sellingPrice)}!`, { variant: 'error' })
    //   };
    // };

    // Check for quantity field as well
    if (field === 'quantity') {

      if (fieldValue > sales.products[index].product.quantity) {
        fieldValue = sales.products[index].product.quantity;
        enqueueSnackbar(`There are only ${sales.products[index].product.quantity} units available.`, { variant: 'error' })
      };

    };

    // Create a copy of the products array
    const updatedProducts = [...sales.products];

    // Access the specific product by index
    const product = { ...updatedProducts[index] };

    // Update the field with the new value
    product[field] = fieldValue;

    // Update the products array with the modified product
    updatedProducts[index] = product;

    console.log("new Product", updatedProducts[index]);


    // Update the state immutably
    setSales(prevSales => ({
      ...prevSales,
      products: updatedProducts
    }));
  };

  const handleEdit = () => {
    setAnchorEl(null);
    console.log({ tempData });

    setSales(prevSales => ({
      ...prevSales,
      customer: tempData.customerId,
      mobileNumber: tempData.customerId.mobileNumber.slice(3),
      salesMan: tempData.salesManId,
      invoiceDate: dayjs(tempData.invoiceDate),
      invoiceDueDate: dayjs(tempData.invoiceDueDate),
      products: tempData.products,
      // amountToPay: tempData.remainingAmount,
      // totalAmount: tempData.totalAmount,
      paymentMode: tempData.payments[tempData.payments.length - 1].paymentID.paymentMode,
      paymentStatus: tempData.paymentStatus,
      // bank: tempData.payments[0].paymentID.bank,
      ...(tempData?.discount?.offer && { offer: tempData.discount.offer.id }) // Conditionally add the offer object
    }));
    setEditSales(true);
    setOpen(true);
  }

  const handleConfirmDelete = () => {

    const decryptedToken = localStorage.getItem(constants.keyUserToken);
    const accessToken = decryptToken(decryptedToken);
    const config = {
      headers: {
        Authorization: `Bearer ${accessToken}`
      }
    };
    axios.post(endpoints.sales.delete, { id: tempData._id }, config)
      .then((res) => {
        if (res) {
          enqueueSnackbar('Sales order deleted successfully!', { variant: 'success' });
          setOpenDialog(false);
          setTempData({});
          fetchSales();
          fetchSaleSummary();
        };
      }).catch((err) => {
        console.log(err)
        enqueueSnackbar(err.msg || err.error, { variant: 'error' });
      })
  };

  const productColumns = [
    {
      name: 'Product Name',
      selector: row => row?.product?.name ?? row.name,

    },
    {
      name: 'Quantity',
      selector: row => row.quantity,

      cell: (row, index) => (
        <TextField
          disabled={editSales}
          type='number'
          onBlur={(e) => {
            // Create a copy of the products array
            const updatedProducts = [...sales.products];

            // Access the specific product by index
            const product = { ...updatedProducts[index] };

            if (!row.quantity) {
              enqueueSnackbar(`Quantity cannot be empty`, { variant: 'error' });
              product.quantity = '1';
              // Update the products array with the modified product
              updatedProducts[index] = product;

              // Update the state immutably
              setSales(prevSales => ({
                ...prevSales,
                products: updatedProducts
              }));
            }
          }}
          onChange={(e) => handleProductChange(index, 'quantity', e.target.value)}
          value={row.quantity} size='small' />
      ),
      center: 'true'
    },
    {
      name: 'Sell Price',
      selector: row => row.sellingPrice,
      cell: (row, index) => (
        <TextField
          type='number'
          disabled={editSales}
          onBlur={() => {
            // Create a copy of the products array
            const updatedProducts = [...sales.products];

            // Access the specific product by index
            const product = { ...updatedProducts[index] };


            if (!row.sellingPrice || product.product.purchasePrice > parseInt(row.sellingPrice, 10)) {
              enqueueSnackbar(`The product price cannot be lower than its purchase price of ${format(sales.products[index].product.purchasePrice)}`, { variant: 'error' })
              // Update the field with the new value
              product.sellingPrice = String(product.product.purchasePrice);

              // Update the products array with the modified product
              updatedProducts[index] = product;

              // Update the state immutably
              setSales(prevSales => ({
                ...prevSales,
                products: updatedProducts
              }));
            }
          }}
          onChange={(e) => handleProductChange(index, 'sellingPrice', e.target.value)}
          value={row.sellingPrice}
          size='small' />
      ),
      center: 'true'
    },
    {
      name: 'GST Value',
      selector: row => row?.product?.gstValue ?? row.gstValue,
      center: 'true'
    },
    {
      name: 'Discount',
      selector: row => row.discount,
      cell: (row, index) => (
        <TextField value={row.discount}
          size='small'
          disabled={editSales}
          type='number'
          onBlur={() => {
            if (row.discount.length === 0) {
              // Create a copy of the products array
              const updatedProducts = [...sales.products];

              // Access the specific product by index
              const product = { ...updatedProducts[index] };

              // Update the field with the new value
              product.discount = '0';

              // Update the products array with the modified product
              updatedProducts[index] = product;

              // Update the state immutably
              setSales(prevSales => ({
                ...prevSales,
                products: updatedProducts
              }));
            }
          }}
          onChange={e => handleProductChange(index, 'discount', e.target.value)}
          InputProps={{
            endAdornment: (
              <InputAdornment position="end">
                <PercentIcon fontSize='small' />
              </InputAdornment>
            ),
          }} />
      ),
      center: 'true'
    },
    {
      name: 'Net Amount',
      selector: row => {
        const quantity = parseInt(!row.quantity ? row.quantity : 0, 10);
        const sellingPrice = parseInt(!row.sellingPrice ? row.sellingPrice : 0, 10);
        const netAmount = quantity * sellingPrice;
        // console.log(" ~ SalesView ~ netAmount:", netAmount)
        // const gstPercentage = parseFloat(row.product.gstValue ? row.product.gstValue : 0); // Assuming gstValue is a string representing percentage
        // const gstAmount = (netAmount * gstPercentage) / 100;
        // return netAmount + gstAmount;
        return netAmount;
      },
      center: 'true'
    },
    {
      name: 'Actions',
      cell: (row, index) => (
        <IconButton disabled={editSales} onClick={() => handleDeleteProduct(row, index)} color='error' variant='outlined'>
          <DeleteIcon />
        </IconButton>
      ),
      center: 'true'
    },
  ];

  // const data = [
  //   {
  //     customerName: 'Disha Shaha',
  //     productName: "XXXXX",
  //     amount: "XXX",
  //     mode: "Cash",
