  //   },
  //   {
  //     customerName: 'Disha Shaha',
  //     productName: "XXXXX",
  //     amount: "XXX",
  //     mode: "Cash",
  //     status: "Paid",
  //     number: "XXX",
  //     lastUpdated: "28 Dec 2023"

  //   },
  //   {
  //     customerName: 'Disha Shaha',
  //     productName: "XXXXX",
  //     amount: "XXX",
  //     mode: "Cash",
  //     status: "Paid",
  //     number: "XXX",
  //     lastUpdated: "28 Dec 2023"

  //   },
  //   {
  //     customerName: 'Disha Shaha',
  //     productName: "XXXXX",
  //     amount: "XXX",
  //     mode: "Cash",
  //     status: "Paid",
  //     number: "XXX",
  //     lastUpdated: "28 Dec 2023"

  //   },
  // ];

  const { format } = new Intl.NumberFormat('hi-In', {
    style: 'currency',
    currency: 'INR'
  });

  const addProductToSales = (newValue) => {

    // Check if the product is already added to sales
    if (sales.products.find((product) => product.product.name === newValue.name)) {
      enqueueSnackbar('Product already added!', { variant: 'error' });
      return;
    }

    // Check if product quantity is available
    const existingProduct = products.find((product) => product.name === newValue.name);
    if (existingProduct && existingProduct.quantity < newValue.quantity) {
      enqueueSnackbar('Product quantity not available!', { variant: 'error' });
      return;
    }

    // Add product to sales
    const productToAdd = {
      product: newValue,
      sellingPrice: newValue.sellingPrice,
      quantity: 1,
      category: newValue.category,
      discount: 0, // In percentage
      // netAmount: newValue.product.sellingPrice * newValue.quantity
    };

    setSales((prev) => ({
      ...prev,
      products: [...prev.products, productToAdd]
    }));

    console.log(newValue);
    setSaleProduct(initialSaleProduct);
  };

  // Function to calculate the discount
  function calculateDiscount(product) {
    const totalAmount = parseInt(!product.sellingPrice ? 0 : product.sellingPrice, 10) * parseInt(!product.quantity ? 0 : product.quantity, 10);
    const discountAmount = totalAmount * (parseInt(!product.discount ? 0 : product.discount, 10) / 100); // Assuming discount is in percentage
    return discountAmount;
  };


  const handleSaveSalesOrder = () => {

    const salesPayload = { ...sales };

    salesPayload.discount = {};
    salesPayload.discount.productDiscount = productDiscount;
    if (Object.keys(sales.offer).length > 0) {
      salesPayload.discount.offer = {};
      salesPayload.discount.offer.discount = offerDiscount;
      salesPayload.discount.offer.id = sales.offer;
    };

    salesPayload.totalDiscount = productDiscount + offerDiscount;
    salesPayload.totalAmount = totalAmount;
    salesPayload.invoiceDueDate = dayjs(salesPayload.invoiceDueDate).format('YYYY-MM-DD');

    // If no errors, proceed with saving
    const decryptedToken = localStorage.getItem(constants.keyUserToken);
    const accessToken = decryptToken(decryptedToken);
    axios.post(endpoints.sales.add, salesPayload,
      {
        headers: {
          Authorization: `Bearer ${accessToken}`,
        }
      })
      .then((res) => {
        enqueueSnackbar('Sales order created successfully!', { variant: 'success' });
        setOpen(false);
        handleClearAllStates();
        fetchSales();
        fetchSaleSummary();
      }).catch((err) => {
        enqueueSnackbar(err.error || err.err, { variant: 'error' });
      })

  };

  const handleUpdateSalesOrder = () => {

    const updatePayload = {};

    updatePayload.invoiceDueDate = dayjs(sales.invoiceDueDate).format('YYYY-MM-DD');
    updatePayload.paymentStatus = sales.paymentStatus;
    updatePayload.id = tempData._id;
    console.log({ updatePayload });

    // If no errors, proceed with saving
    const decryptedToken = localStorage.getItem(constants.keyUserToken);
    const accessToken = decryptToken(decryptedToken);
    axios.post(endpoints.sales.update, updatePayload,
      {
        headers: {
          Authorization: `Bearer ${accessToken}`,
        }
      })
      .then((res) => {
        enqueueSnackbar('Sales order updated successfully!', { variant: 'success' });
        setOpen(false);
        setTempData({});
        handleClearAllStates();
        fetchSales();
        setEditSales(false);
      }).catch((err) => {
        enqueueSnackbar(err.error || err.err, { variant: 'error' });
      })
  }

  const productDiscount = sales.products.reduce((total, product) => {
    const discount = calculateDiscount(product);
    return total + discount;
  }, 0)

  const netTotalAmountWithGST = sales.products.reduce((total, product) => {
    const sellingPrice = parseInt(!product.sellingPrice ? 0 : product.sellingPrice, 10);
    const quantity = parseInt(!product.quantity ? 0 : product.quantity, 10);
    // const gstPercentage = parseFloat(product.product.gstValue); // Assuming gstValue is a string representing percentage
    const netAmount = sellingPrice * quantity;
    // const gstAmount = (netAmount * gstPercentage) / 100;
    // const totalAmountWithGST = netAmount + gstAmount;
    const totalAmountWithGST = netAmount;
    return total + totalAmountWithGST;
  }, 0);


  let offerDiscount = 0;
  if (Object.keys(sales.offer).length > 0) {
    if (sales.offer.type === 'PERCENTAGE') {
      offerDiscount = (netTotalAmountWithGST * sales.offer.typeValue) / 100;
    } else {
      offerDiscount = sales.offer.typeValue;
    }
  }

  const totalAmount = netTotalAmountWithGST - (productDiscount + Number(offerDiscount));



